[data-controls] {
  display: block;
  margin: 40px auto; }

[data-controls] > *, pause {
  background-color: black;
  color: yellow;
  font-weight: bold;
  font-size: 100px;
  border-radius: 100px; }

[data-left], [data-right] {
  display: inline-block;
  margin: 0 50px;
  padding: 0 25px; }

[data-up], [data-down] {
  display: block;
  width: 100px;
  margin: auto; }

[data-down] {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg); }

[data-pause] {
  display: block;
  width: 100px;
  margin: auto;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg); }



